<template>
  <div id="acl-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="aclFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('OBJECT_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="objectTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in objectTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full required" v-if="objectTypeEnumId === 'OBJ_REPORT'">
          <label class="vs-input--label">{{ $t('OBJECT_ID') }}</label>
          <report-lookup :title="$t('REPORT')" valueField="pseudoId" displayField="reportName" @select-record="selectReportRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required" v-else>
          <label class="vs-input--label">{{ $t('OBJECT_ID') }}</label>
          <vs-input class="w-full" v-model="objectId" :disabled="objectTypeEnumId === 'OBJ_ANY'" />
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('PERMISSION_TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="permissionTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in permissionTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('POSITION') }}</label>
          <div class="flex">
            <vs-input-number v-model="position" min="1" :disabled="objectTypeEnumId === 'OBJ_ANY'"/>
          </div>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createUserGroupAcl" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideAclForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="aclTableData" :noDataText="aclTableNoDataText" :sst="true" search @search="filterAclTable" @sort="orderAclTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showAclForm" :disabled="aclFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshAclTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="aclTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ aclTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="aclTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="position">#</vs-th>
        <vs-th>
          {{ this.$t('OBJECT_TYPE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('OBJECT_ID') }}
        </vs-th>
        <vs-th>
          {{ this.$t('PERMISSION_TYPE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th>
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th v-if="$acl.check('isNucleusAll')">
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].position">
            {{ data[index].position }}
          </vs-td>
          <vs-td :data="data[index].objectTypeDescription">
            {{ data[index].objectTypeDescription }}
          </vs-td>
          <vs-td :data="data[index].objectId">
            {{ data[index].objectId | placeholder('-') }}
          </vs-td>
          <vs-td :data="data[index].permissionTypeDescription">
            {{ data[index].permissionTypeDescription }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].toDate">
            {{ data[index].toDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].options" v-if="$acl.check('isNucleusAll')">
            <div class="flex">
              <vs-button class="p-1 mr-1" color="primary" v-if="data[index].objectTypeEnumId !== 'OBJ_ANY'" @click="moveUp(data[index])" icon-pack="far" icon="fa-arrow-up"></vs-button>
              <vs-button class="p-1 mr-1" color="primary" v-if="data[index].objectTypeEnumId !== 'OBJ_ANY'" @click="moveDown(data[index])" icon-pack="far" icon="fa-arrow-down"></vs-button>
              <vs-button class="p-3 mr-1" color="danger" @click="expireUserGroupAcl(data[index])">{{ $t('EXPIRE') }}</vs-button>
            </div>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="aclTableTotalPages" v-model="aclTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import AclTableMixin from '@/mixins/nucleus/AclTableMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import EnumerationService from '@/services/commons/EnumerationService'
import Datepicker from 'vuejs-datepicker'
import ReportLookup from '@/views/report/report/ReportLookup'

export default {
  name: 'user-group-acl',
  mixins: [AclTableMixin, DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    Datepicker,
    ReportLookup
  },
  props: {
    userGroupId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      aclTableData: [],
      aclTableFilter: null,
      aclTableFilterTimer: null,
      aclTableOrderBy: 'position',
      aclTableContainer: 'acl-loading-container',
      aclTableBusy: false,
      aclTableTotalRows: 0,
      aclTableCurrentPage: 1,
      aclTablePerPage: 10,
      aclFormVisible: false,
      position: 1,
      objectId: '',
      objectTypeEnumId: 'OBJ_ANY',
      objectTypes: [],
      permissionTypeEnumId: 'PERM_NONE',
      permissionTypes: [],
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    aclTableNoDataText: function () {
      return this.aclTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    aclTableTotalPages: function () {
      return Math.ceil(this.aclTableTotalRows / this.aclTablePerPage)
    },
    aclTablePagingInfo: function () {
      return this.pagingInfo(this.aclTableCurrentPage, this.aclTablePerPage, this.aclTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'ObjectType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.objectTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'PermissionType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.permissionTypes = response.data.enumerationList || []
      })
    },
    loadAclTableData (loadingContainerId) {
      let self = this
      this.aclTableBusy = true
      this.aclTableContainer = loadingContainerId || this.aclTableContainer
      this.startLoading(this.aclTableContainer)
      SecurityService.userGroupAcls({
        pageIndex: this.aclTableCurrentPage - 1,
        pageSize: this.aclTablePerPage,
        filter: this.aclTableFilter,
        orderByField: this.aclTableOrderBy,
        userGroupId: this.userGroupId
      }).then(function (response) {
        self.stopLoading(self.aclTableContainer)
        self.aclTableBusy = false
        self.aclTableTotalRows = response.data.totalRows
        self.aclTableData = response.data.userGroupAclList || []
      })
    },
    refreshAclTable () {
      this.loadAclTableData()
    },
    filterAclTable (filter) {
      let self = this
      if (this.aclTableFilter !== filter) {
        clearTimeout(this.aclTableFilterTimer)
        this.aclTableFilterTimer = setTimeout(() => {
          self.aclTableFilter = filter
          self.loadAclTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.aclTableFilterTimer)
      }
    },
    orderAclTable (orderBy, active) {
      this.aclTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAclTableData()
    },
    showAclForm () {
      this.aclFormVisible = true
    },
    hideAclForm () {
      this.aclFormVisible = false
    },
    createUserGroupAcl () {
      let self = this
      SecurityService.createUserGroupAcl({
        userGroupId: this.userGroupId,
        position: this.position,
        objectId: this.objectId,
        objectTypeEnumId: this.objectTypeEnumId,
        permissionTypeEnumId: this.permissionTypeEnumId,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshAclTable()
        self.hideAclForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expireUserGroupAcl (acl) {
      let self = this
      SecurityService.expireUserGroupAcl({
        userGroupId: this.userGroupId,
        itemId: acl.itemId
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshAclTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    moveUp (report) {
      let self = this
      SecurityService.modifyUserGroupAcl({
        userGroupId: this.userGroupId,
        itemId: report.itemId,
        position: (report.position - 1)
      }).then(function (response) {
        self.refreshAclTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    moveDown (report) {
      let self = this
      SecurityService.modifyUserGroupAcl({
        userGroupId: this.userGroupId,
        itemId: report.itemId,
        position: (report.position + 1)
      }).then(function (response) {
        self.refreshAclTable()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    selectReportRecord (record) {
      this.objectId = record.pseudoId
    }
  },
  mounted () {
    this.load()
    this.loadAclTableData()
  },
  watch: {
    aclTableCurrentPage: function () {
      this.refreshAclTable()
    },
    aclTablePerPage: function () {
      this.refreshAclTable()
    },
    objectTypeEnumId: function () {
      this.objectId = ''
    }
  }
}
</script>
