<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
      </div>
      <div class="row hover mb-base">
        <p class="info-row">
          <span>{{ $t('ID') }}:</span>
          <span>{{ userGroup.userGroupId }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('GROUP_NAME') }}:</span>
          <span>{{ userGroup.description | placeholder('-') }}</span>
        </p>
        <p class="info-row">
          <span>{{ $t('TYPE') }}:</span>
          <span>{{ userGroup.groupTypeDescription | placeholder('-') }}</span>
        </p>
        <p class="info-row" v-if="userGroup.ipAllowed">
          <span>{{ $t('IPS_ALLOWED') }}:</span>
          <span>{{ userGroup.ipAllowed | placeholder('-') }}</span>
        </p>
      </div>

      <div class="code-editor p-4 mb-base" v-if="userGroup.wiki">
        <markdown-it-vue :content="userGroup.wiki"/>
      </div>

      <vs-tabs>
        <vs-tab :label="$t('MEMBERS')">
          <user-group-member @show-alert="showAlert" :userGroupId="userGroupId"/>
        </vs-tab>
        <vs-tab :label="$t('ACL')">
          <user-group-acl @show-alert="showAlert" :userGroupId="userGroupId"/>
        </vs-tab>
        <vs-tab :label="$t('WORKFLOW')">
          <user-group-workflow @show-alert="showAlert" :userGroupId="userGroupId"/>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import UserGroupMember from '@/views/nucleus/user-group/user-group-view/UserGroupMember'
import UserGroupAcl from '@/views/nucleus/user-group/user-group-view/UserGroupAcl'
import UserGroupWorkflow from '@/views/nucleus/user-group/user-group-view/UserGroupWorkflow'
import MarkdownItVue from 'markdown-it-vue'

export default {
  name: 'user-group-view',
  mixins: [AlertMixin],
  components: {
    UserGroupMember,
    UserGroupAcl,
    UserGroupWorkflow,
    MarkdownItVue
  },
  data: function () {
    return {
      userGroup: {},
      userGroupId: this.$route.params.id,
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      SecurityService.userGroups({
        userGroupId: this.$route.params.id
      }).then(function (response) {
        self.userGroup = response.data.userGroupList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyUserGroup', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  }
}
</script>
