<template>
  <div id="user-loading-container" class="vs-con-loading__container">
    <div class="inline-form p-4 mb-base" v-if="userFormVisible">
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('USER') }}</label>
          <user-lookup :title="$t('USER')" valueField="userId" displayField="userFullName" @select-record="selectUserRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col sm:w-1/2 w-full">
          <div class="flex justify-left">
            <vs-button class="px-5 py-3 mr-2" @click="createUserGroupMember" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
            <vs-button class="px-5 py-3 mr-2" @click="hideUserForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
          </div>
        </div>
      </div>
    </div>

    <vs-table class="mb-base" :data="userTableData" :noDataText="userTableNoDataText" :sst="true" search @search="filterUserTable" @sort="orderUserTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="showUserForm" :disabled="userFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
          <vs-button class="p-3 mr-4" @click="refreshUserTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="userTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ userTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="userTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="userId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="userFullName">
          {{ this.$t('USER_FULL_NAME') }}
        </vs-th>
        <vs-th sort-key="username">
          {{ this.$t('USERNAME') }}
        </vs-th>
        <vs-th sort-key="fromDate">
          {{ this.$t('FROM_DATE') }}
        </vs-th>
        <vs-th sort-key="thruDate">
          {{ this.$t('TO_DATE') }}
        </vs-th>
        <vs-th v-if="$acl.check('isNucleusAll')">
          {{ this.$t('OPTIONS') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].userId">
            <router-link :to="{ name: 'ViewUser', params: { id: data[index].userId } }">{{ data[index].userId }}</router-link>
          </vs-td>
          <vs-td :data="data[index].userFullName">
            {{ data[index].userFullName }}
          </vs-td>
          <vs-td :data="data[index].username">
            {{ data[index].username }}
          </vs-td>
          <vs-td :data="data[index].fromDate">
            {{ data[index].fromDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].thruDate">
            {{ data[index].thruDate | formatUnixDate }}
          </vs-td>
          <vs-td :data="data[index].options" v-if="$acl.check('isNucleusAll')">
            <vs-button class="p-3 mr-4" color="danger" @click="expireUserGroupMember(data[index])">{{ $t('EXPIRE') }}</vs-button>
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="userTableTotalPages" v-model="userTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import UserTableMixin from '@/mixins/nucleus/UserTableMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import UserLookup from '@/views/nucleus/user/UserLookup'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'user-group-member',
  mixins: [DatePickerMixin, LoadingMixin, ListLayoutMixin, UserTableMixin],
  components: {
    UserLookup,
    Datepicker
  },
  props: {
    userGroupId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      userTableData: [],
      userTableFilter: null,
      userTableFilterTimer: null,
      userTableOrderBy: null,
      userTableContainer: 'user-loading-container',
      userTableBusy: false,
      userTableTotalRows: 0,
      userTableCurrentPage: 1,
      userTablePerPage: 10,
      userFormVisible: false,
      userId: '',
      fromDate: null,
      toDate: null
    }
  },
  computed: {
    userTableNoDataText: function () {
      return this.userTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    userTableTotalPages: function () {
      return Math.ceil(this.userTableTotalRows / this.userTablePerPage)
    },
    userTablePagingInfo: function () {
      return this.pagingInfo(this.userTableCurrentPage, this.userTablePerPage, this.userTableTotalRows)
    }
  },
  methods: {
    loadUserTableData (loadingContainerId) {
      let self = this
      this.userTableBusy = true
      this.userTableContainer = loadingContainerId || this.userTableContainer
      this.startLoading(this.userTableContainer)
      SecurityService.userGroupMembers({
        pageIndex: this.userTableCurrentPage - 1,
        pageSize: this.userTablePerPage,
        filter: this.userTableFilter,
        orderByField: this.userTableOrderBy,
        userGroupId: this.userGroupId
      }).then(function (response) {
        self.stopLoading(self.userTableContainer)
        self.userTableBusy = false
        self.userTableTotalRows = response.data.totalRows
        self.userTableData = response.data.userGroupMemberList || []
      })
    },
    refreshUserTable () {
      this.loadUserTableData()
    },
    filterUserTable (filter) {
      let self = this
      if (this.userTableFilter !== filter) {
        clearTimeout(this.userTableFilterTimer)
        this.userTableFilterTimer = setTimeout(() => {
          self.userTableFilter = filter
          self.loadUserTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.userTableFilterTimer)
      }
    },
    orderUserTable (orderBy, active) {
      this.userTableOrderBy = (active ? '' : '-') + orderBy
      this.loadUserTableData()
    },
    showUserForm () {
      this.userFormVisible = true
    },
    hideUserForm () {
      this.userFormVisible = false
    },
    selectUserRecord (record) {
      this.userId = record.userId || ''
    },
    createUserGroupMember () {
      let self = this
      SecurityService.createUserGroupMember({
        userGroupId: this.userGroupId,
        userId: this.userId,
        fromDate: this.getTime(this.fromDate),
        toDate: this.getTime(this.toDate)
      }).then(function (response) {
        self.refreshUserTable()
        self.hideUserForm()
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    expireUserGroupMember (user) {
      let self = this
      SecurityService.expireUserGroupMember({
        userGroupId: this.userGroupId,
        userId: user.userId,
        fromDate: user.fromDate
      }).then(function (response) {
        if (response.status === 200) {
          self.refreshUserTable()
        }
      }).catch(function (error) {
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    }
  },
  mounted () {
    this.loadUserTableData()
  },
  watch: {
    userTableCurrentPage: function () {
      this.refreshUserTable()
    },
    userTablePerPage: function () {
      this.refreshUserTable()
    }
  }
}
</script>
