<template>
  <div id="workflow-loading-container" class="vs-con-loading__container">
    <vs-table class="mb-base" :data="workflowTableData" :noDataText="workflowTableNoDataText" :sst="true" search @search="filterWorkflowTable" @sort="orderWorkflowTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3 mr-4" @click="refreshWorkflowTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="workflowTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ workflowTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="workflowTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="workflowId">
          {{ this.$t('ID') }}
        </vs-th>
        <vs-th sort-key="workflowName">
          {{ this.$t('WORKFLOW_NAME') }}
        </vs-th>
        <vs-th sort-key="workflowTypeName">
          {{ this.$t('WORKFLOW_TYPE') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].workflowId">
            <router-link :to="{ name: 'ViewWorkflow', params: { id: data[index].workflowId } }">{{ data[index].workflowId }}</router-link>
          </vs-td>
          <vs-td :data="data[index].workflowName">
            {{ data[index].workflowName }}
          </vs-td>
          <vs-td :data="data[index].workflowTypeName">
            {{ data[index].workflowTypeName }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="workflowTableTotalPages" v-model="workflowTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import SecurityService from '@/services/nucleus/SecurityService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

export default {
  name: 'user-group-workflow',
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    userGroupId: {
      type: String,
      required: true
    }
  },
  data: function () {
    return {
      workflowTableData: [],
      workflowTableFilter: null,
      workflowTableFilterTimer: null,
      workflowTableOrderBy: null,
      workflowTableContainer: 'workflow-loading-container',
      workflowTableBusy: false,
      workflowTableTotalRows: 0,
      workflowTableCurrentPage: 1,
      workflowTablePerPage: 10
    }
  },
  computed: {
    workflowTableNoDataText: function () {
      return this.workflowTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    workflowTableTotalPages: function () {
      return Math.ceil(this.workflowTableTotalRows / this.workflowTablePerPage)
    },
    workflowTablePagingInfo: function () {
      return this.pagingInfo(this.workflowTableCurrentPage, this.workflowTablePerPage, this.workflowTableTotalRows)
    }
  },
  methods: {
    loadWorkflowTableData (loadingContainerId) {
      let self = this
      this.workflowTableBusy = true
      this.workflowTableContainer = loadingContainerId || this.workflowTableContainer
      this.startLoading(this.workflowTableContainer)
      SecurityService.userGroupWorkflowInitiators({
        pageIndex: this.workflowTableCurrentPage - 1,
        pageSize: this.workflowTablePerPage,
        filter: this.workflowTableFilter,
        orderByField: this.workflowTableOrderBy,
        userGroupId: this.userGroupId
      }).then(function (response) {
        self.stopLoading(self.workflowTableContainer)
        self.workflowTableBusy = false
        self.workflowTableTotalRows = response.data.totalRows
        self.workflowTableData = response.data.workflowInitiatorList || []
      })
    },
    refreshWorkflowTable () {
      this.loadWorkflowTableData()
    },
    filterWorkflowTable (filter) {
      let self = this
      if (this.workflowTableFilter !== filter) {
        clearTimeout(this.workflowTableFilterTimer)
        this.workflowTableFilterTimer = setTimeout(() => {
          self.workflowTableFilter = filter
          self.loadWorkflowTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.workflowTableFilterTimer)
      }
    },
    orderWorkflowTable (orderBy, active) {
      this.workflowTableOrderBy = (active ? '' : '-') + orderBy
      this.loadWorkflowTableData()
    }
  },
  mounted () {
    this.loadWorkflowTableData()
  },
  watch: {
    workflowTableCurrentPage: function () {
      this.refreshWorkflowTable()
    },
    workflowTablePerPage: function () {
      this.refreshWorkflowTable()
    }
  }
}
</script>
