<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="userTableData" :noDataText="userTableNoDataText" :sst="true" search @search="filterUserTable" @sort="orderUserTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadUserTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="userTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ userTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="userTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="userId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="userFullName">
            {{ this.$t('USER_FULL_NAME') }}
          </vs-th>
          <vs-th sort-key="username">
            {{ this.$t('USERNAME') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].userId">
              {{ data[index].userId }}
            </vs-td>
            <vs-td :data="data[index].userFullName">
              <span :class="disabledClass(data[index])">{{ data[index].userFullName | placeholder('-') }}</span>
            </vs-td>
            <vs-td :data="data[index].username">
              {{ data[index].username }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="userTableTotalPages" v-model="userTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import UserTableMixin from '@/mixins/nucleus/UserTableMixin'

export default {
  name: 'user-popup',
  mixins: [PopupMixin, UserTableMixin],
  mounted: function () {
    this.loadUserTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
